@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
* {
  font-family: "Montserrat", sans-serif;
}

nav {
  background-color: #b33939;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 10px 20px;
}

nav .navbar-brand {
  text-decoration: none;
  color: #ffffff;
  font-weight: 600;
  font-size: 20px;
}

nav ul {
  display: flex;
  flex-direction: row;
}

nav ul .nav-links {
  color: #ffffff;
  text-decoration: none;
  margin: 5px 20px;
}

nav ul .nav-links li {
  list-style: none;
}

nav ul .nav-links:hover {
  font-weight: 600;
}

nav ul .active {
  font-weight: 600;
}
